.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid black;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
}
.Button:focus {
    outline: none;
}
.Button:active {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
}
.Button:disabled {
    background: #ccc;
    color: black;
    cursor: not-allowed;
}
.success {
    background: rgb(108, 175, 42);
}
.error {
    background: crimson;
}
.primary {
    background: rgb(150, 150, 250);
}