.FinishedQuiz {
    padding: 20px;
    border: 2px solid black;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
}
.FinishedQuiz ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.FinishedQuiz ul li i{
    margin-left: 10px;
}

.success {
    color: rgb(108, 175, 42);
}
.error {
    color: crimson;
}